import React, { Component } from 'react';
import _ from 'lodash';

class Vertical extends Component {

  get sideHandle() {
    return (
      <div
        className="side-handle"
        onClick={ () => this.props.toggleExpansion(this.props.position) }
        style={{[`border${_.upperFirst(this.props.position)}`]: '1px solid #d2d4d7'}}
      >
        <i
          className="fa fa-ellipsis-v"
        ></i>
      </div>
    )
  };

  get orderedContent() {
    if (this.props.position !== 'right') {
      return (
        <div style={{display: 'flex'}}>
          { this.props.content }
          { this.sideHandle }
        </div>
      )
    }
    return (
        <div style={{display: 'flex'}}>
          { this.sideHandle }
          { this.props.content }
        </div>
      )
  }

  render() {
    return (
      <div
      	className="vertical-bar"
      	style={{
          width: this.props.expanded ? 'calc(100vw - 355px)' : 20,
          backgroundColor: this.props.barColor,
          justifyContent: this.props.position === 'right' ? 'flex-start' : 'flex-end'
        }}
      >
        {this.orderedContent}
      </div>
    );
  }
}

export default Vertical;



