import React, { Component } from 'react';

class Horizontal extends Component {

  render() {
    return (
      <div
        className="horizontal-bar"
        style={{height: this.props.expanded ? 300 : 20, backgroundColor: 'black'}}
        onClick={ () => this.props.toggleExpansion(this.props.position) }
      >
      </div>
    );
  }
}

export default Horizontal;



